<template>
	<div class="myorder">
		
		<el-table :data="tableData" style="width: 100%">
			<el-table-column prop="order_id" label="订单ID" width="180">

			</el-table-column>
			<el-table-column prop="order_no" label="订单编号" width="180">
			</el-table-column>
			<el-table-column prop="create_time" label="订单时间">
			</el-table-column>

			<el-table-column prop="buy_num" label="购买数量">
			</el-table-column>

			<el-table-column prop="goods_name" label="商品名称">
			</el-table-column>

			<el-table-column prop="price" label="订单价格">
			</el-table-column>

		</el-table>

		<div class="any-flex pagein">
			<div></div>
			<el-pagination background prev-text='上一页' :total='total' next-text='下一页' layout="prev, pager, next" @current-change='changepages'
			 :current-page='currentPage' :page-size='pagesize'>
			</el-pagination>
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				currentPage: 0,
				total: 0,
				pagesize: 0,
				tableData: []
			}
		},
		methods: {
			init() {
				this.utils.post({
					url: 'admin_reseller_order',
					data: {
						shop_id: this.global.shop_id()
					}
				}).then(res => {
					
					this.total = res.total
					this.currentPage = res.current_page
					this.pagesize = res.per_page
					res.data.forEach((item,index)=>{
						item.create_time = this.global.timelaver(item.create_time * 1000)
					})
					this.tableData = res.data
				})
			},
			changepages(e){
				this.utils.post({
					url: 'admin_reseller_order',
					data: {
						shop_id: this.global.shop_id()
					}
				}).then(res => {  
					res.data.forEach((item,index)=>{
						item.create_time = this.global.timelaver(item.create_time * 1000)
					})
					this.tableData = res.data
				})
			}
			
		},
		mounted() {
			this.init()
		}
	}
</script>

<style lang="scss">
	.pagein {
		padding: 20px 90px;
		justify-content: space-between;
	}
</style>
